import { Notify } from 'notiflix/build/notiflix-notify-aio';

const refs = {
  form: document.querySelector('.form'),
  delay: document.querySelector('input[name=delay]'),
  step: document.querySelector('input[name=step]'),
  amount: document.querySelector('input[name=amount]'),
  createBtn: document.querySelector('button'),
};

refs.form.addEventListener('submit', onFormSubmit);

function createPromise(position, delay) {
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      const shouldResolve = Math.random() > 0.3;
      if (shouldResolve) {
        resolve({ position, delay });
      } 
      else {
        reject({ position, delay });
      }
      },delay);
  });
};

function onFormSubmit(evt) {
  evt.preventDefault();

  let evtDelay = Number(refs.delay.value);
  let evtStep = Number(refs.step.value);
  let evtAmount = Number(refs.amount.value);

  for (let value = 1; value <= evtAmount; value += 1) {
    createPromise(value, evtDelay)
      .then(({ position, delay }) => {
        Notify.success(`✅ Fulfilled promise ${position} in ${delay}ms`);
      })
      .catch(({ position, delay }) => {
        Notify.failure(`❌ Rejected promise ${position} in ${delay}ms`);
      });
    evtDelay += evtStep;

    if (onFormSubmit) {
      refs.createBtn.setAttribute('disabled', true)
    };
  };

  let removeDisabledBtn = null;

  removeDisabledBtn = setTimeout(() =>
  { refs.createBtn.removeAttribute('disabled', true) }, (3000 + evtDelay + evtStep) - evtStep * 2);
};